import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/index/index'
import homeList from '@/views/homeList/homeList'
import homeList2 from '@/views/homeList/homeList2'
import classify from '@/views/homeList/classify'
import listdetail from '@/views/listdetail/listdetail'
import study from '@/views/study/study'
import studyaudio from '@/views/studyaudio/studyaudio'
import practice from '@/views/practice/practice'
import test from '@/views/test/test'
import comment from '@/views/comment'
import mine from '@/views/mine'
import reading from '@/views/reading'
import rank from '@/views/rank'
import collection from '@/views/collection'
import task from '@/views/task'
Vue.use(Router)

export default new Router({
  routes: [
    {
      //
      path: '/',
      name: 'index',
      meta:{title:'-'},

      component: index
    },
    //
    {
      path: '/homeList',
      name: 'homeList',
      meta:{
      title:'',
      },
      component: homeList
    },{
      path: '/classify',
      name: 'classify',
      meta:{
      title:'',
      },
      component: classify
    },
    ,{
      path: '/homeList2',
      name: 'homeList2',
      meta:{
      title:'',
      },
      component: homeList2
    },
    //
    {
      path: '/listdetail',
      name: 'listdetail',
      component: listdetail
    },
    //-
    {
      path: '/study',
      name: 'study',
      component: study
    },
    //-
    {
      path: '/studyaudio',
      name: 'studyaudio',
      component: studyaudio
    },
    //
    {
      path: '/practice',
      name: 'practice',
      component: practice
    },
    //
    {
      path: '/test',
      name: 'test',
      component: test
    },
    {
      path: '/comment',
      name: 'comment',
      component: comment,
      meta: {
        title: ''
      }

    },
    {
      path: '/mine',
      name: 'mine',
      component: mine,
      meta: {
        title: ''
      }

    },
    {
      path: '/reading',
      name: 'reading',
      component: reading,
    },
    {
      path: '/rank',
      name: 'rank',
      component: rank,
      meta: {
        title: ''
      }
    },
    {
      path: '/collection',
      name: 'collection',
      component: collection,
      meta: {
        title: ''
      }
    },
    {
      path: '/task',
      name: 'task',
      component: task,
      meta: {
        title: ''
      }
    },
  ]
})



